import React from 'react'
import './HomePage.css'
import netflix_logo from '../images/netflix-logo.png'
import arrow from '../images/arrow.png'
import tv from '../images/tv.png'
import video_tv from '../images/video-tv.m4v'
import mobile from '../images/mobile.jpg'
import show_cover from '../images/show-cover.png'
import smartphone from '../images/smarphone.png'
import download from '../images/download.png'
import loading from '../images/loading.png'
import devices from '../images/devices.png'
import video_devices from '../images/video-devices.m4v'
import plus from '../images/plus.png'
import Home from './home/Home'
import SectionOne from './sectionOne/SectionOne'
import SectionTwo from './sectionTwo/SectionTwo'
import Faq from './FAQ/Faq'
import Footer from './footer/Footer'

function HomePage() {
    return (
        <div className='container'>
            <Home/>
            <SectionOne h1={"Enjoy on your TV."} h2={"Watch on Smart TVs, Playstation, Xbox, Chromecast, Apple TV, Blu-ray players, and more."} video={video_tv} img={tv}/>
            <SectionOne h1={"Watch everywhere."} h2={"Stream unlimited movies and TV shows on your phone, tablet, laptop, and TV without paying more."} video={video_devices} img={devices}/>
            <SectionTwo/>
            <Faq/>
            <Footer/>
        </div>
    )
}

export default HomePage
